import React, {useEffect, useState} from "react";
import {Link, useParams} from "react-router-dom";
import { useNavigate } from "react-router-dom";


function QnaDetail() {
    const [qna, setQna] = useState([]);
    const {qnaId} = useParams();
    const navigate = useNavigate();

    //pk    
    // const getData = async () => {
    //     const uri = `/qna/v1/findByQnaId?qnaId=${qnaId}`;
    //     const encoded = encodeURI(uri);
    //     const json = await (
    //         await fetch(encoded)
    //     ).json();
    //     setQna(json);
    // }
    useEffect(() => {

        //pk    
        const getData = async () => {
            const uri = `/qna/v1/findByQnaId?qnaId=${qnaId}`;
            const encoded = encodeURI(uri);
            const json = await (
                await fetch(encoded)
            ).json();
            setQna(json);
        }
        getData();
    },[qna]);




    // delete onclick event
    const onRemove = async () => {
        const confirm = window.confirm("  ?");
        if(confirm === true) {
                const uri = `/qna/v1/deleteQna?qnaId=${qnaId}`;
                const encoded = encodeURI(uri);
                const json = await (
                    await fetch(encoded, {
                        method: 'DELETE',
                        headers: {
                            'Content-Type': 'application/json',
                        }})
                ).json();

                if(json === 1 ) {
                    window.alert(' .');
                    navigate('/qna/qnaBoard');
                }
        }
    }; //remove function end



    return (
        // padding: 5px 1px 2px 3px
        <div>
            <div align="center" style={{ padding : 100 , paddingRight: 330, paddingLeft: 330}}>
                <h1>  detail.</h1>
                <div align="left">
                    <table className="table table-striped table-bordered table-hover">
                        <colgroup>
                            <col width="150px"/>
                            <col/>
                        </colgroup>
                        <tbody>
                        <tr>
                            <th className="active"></th>
                            <td>
                                {qna.qnaId}
                            </td>
                        </tr>
                        <tr>
                            <th className="active"></th>
                            <td>
                                {qna.writer}
                            </td>
                        </tr>
                        <tr>
                            <th className="active"></th>
                            <td>
                                {qna.title}
                            </td>
                        </tr>
                        <tr>
                            <th className="active"></th>
                            <td>
                                {qna.content}
                            </td>
                        </tr>
                        <tr>
                            <th className="active"> </th>
                            <td>
                                {qna.createdDate}
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>

                <div align="right">
                        <button className="btn btn-warning" >
                            <Link to={`/qnaUpdate/${qna.qnaId}/${qna.title}/${qna.writer}/${qna.content}/${qna.createdDate}`}>
                             
                            </Link>
                        </button>
                    <button className="btn btn-danger" onClick={onRemove}> </button>
                </div>
            </div>
        </div>
    )
}

export default QnaDetail;