import {useRef} from "react";
import {useNavigate} from "react-router-dom";


function QnaWrite() {

    const titleRef = useRef(null);
    const contentRef = useRef(null);
    const navigate = useNavigate();

    function onSubmit(e) {

        e.preventDefault();

        const titleVal = titleRef.current.value;
        const contentVal = contentRef.current.value;
        console.log(titleVal);
        console.log(contentVal);

        const uri = `http://localhost:8080/qna/v1/qna`;
        const encoded = encodeURI(uri);

        fetch(encoded, {
            method: "PUT",
            headers: {
                "Content-type": "application/json",
            },
            body: JSON.stringify({
                title: titleVal,
                content: contentVal,
                writer: "xodlf5363"
            }),
        }).then(res => {
            if(res.ok) {
                navigate('/qna/qnaBoard');
            } else {
                alert("  .    ");
            }
        })
    }

    return (
        <div>
            <div align="center" style={{padding : 100, paddingRight: 330, paddingLeft: 330, }}>
                <div >
                    <h1> qna </h1>
                </div>
                <form onSubmit={onSubmit}>
                    <table className="table table-striped table-bordered table-hover">

                        <tbody>
                        <tr>
                            <th scope="row"></th>
                            <td >xodlf5363</td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="3">
                                <input type="text"ref={titleRef} />
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="4" className="view_text">
                                <textarea ref={contentRef}></textarea>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                    <div align="right">
                        <button className="btn btn-warning" ></button>
                    </div>
                </form>
            </div>

        </div>
    )
}

export default QnaWrite;